img {
  max-width: 100%;
}

#sidebar {
  position: fixed;
  height: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #303036;
  left: 0;
  top: 0;
  display: flex;
  z-index: 100;
}
.sidebar_button_menu {
  text-align: center;
  height: 100%;
  width: var(--sidebarButtonWidth);
  background-color: black;
}
.sidebar-link_list {
  height: 100%;
  width: var(--sidebarWidth);
  display: flex;
  flex-direction: column;
}
.sidebar-link_list button {
  font-size: x-large;
  padding: 5px 0;
}

.hamburger_menu {
  display: inline-block;
  cursor: pointer;
  width: 35px;
}

.-bar {
  width: 100%;
  height: 5px;
  background-color: white;
  margin: 6px 0;
  text-align: center;
}

.tab {
  overflow: hidden;
  /* border: 1px solid #ccc; */
  /* background-color: #f1f1f1; */
  background-color: #303036;
  display: flex;
  height: 100%;
  width: 100%;
}

.tab_links {
  border-left: 0.5px solid black;
  border-right: 0.5px solid black;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  white-space: nowrap;
  height: 100%;
  background-color: #29293d;
}

.tab_links button {
  padding: 0 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.tab_links:hover button:last-child {
  visibility: visible;
}

.tab_links button:last-of-type {
  visibility: hidden;
}

.tab_links-selected {
  background-color: #151515;
}

.tab_links:first-child {
  border-left: 0;
}
.tab_links:last-child {
  margin-right: 0;
}

.tab-button-title {
  width: 75%;
  height: 100%;
}

.tab-button-close {
  width: 25%;
  height: 100%;
}

#header {
  right: 0;
  top: 0;
  position: fixed;
  width: calc(100% - var(--leftMargin));

  height: var(--headerHeight);
  z-index: 100;
}

#main_content {
  position: relative;
  left: var(--leftMargin);
  top: var(--headerHeight);
  width: calc(100% - var(--leftMargin) - 1%);
  height: 100%;
  padding: 10px 0 10px 10px;
}

body {
  background-color: #151515;
  color: lightblue;
  font-family: "Courier New", Courier, monospace;
  width: 100%;
  height: 100%;
  --sidebarWidth: 200px;
  --sidebarButtonWidth: 50px;
  --headerHeight: 30px;
  --leftMargin: 250px;
  margin: 0 0;
  top: 0;
  overflow: scroll;
}
a {
  color: lightblue;
}

button {
  color: lightblue;
  cursor: pointer;
}
.text_only_button {
  padding: 0 0;
  border: none;
  background-color: inherit;
}

hr {
  border-top: 1px solid lightblue;
}

.align-center {
  margin-left: auto;
  margin-right: auto;
}

.align-right {
  right: 0;
}

